import React, { useState } from 'react'
import '../HeaderStyle.css';
import { getAuth, signOut } from 'firebase/auth';
import { Link } from 'react-router-dom';
import { useNavigate } from "react-router-dom";
import h1Logo from '../images/h1_logo.png'
import h2Logo from '../images/h2_logo.png'
import menuIcon from '../images/menu.svg'
import CloseIcon from '../images/close.svg'

function Header({ isAuthenticated, setIsAuthenticated }) {
    const navigate = useNavigate();
    
    const handleLogout = () => {
        const auth = getAuth();
        signOut(auth).then(() => {
            navigate('/');
            setIsAuthenticated(false); 
        }).catch((error) => {
            console.error('Logout error:', error);
        });
    };
    
    const [isHovered, setIsHovered] = useState(false); 
    const [visible, setVisible] = useState(false);
    const [openMenu, setOpenMenu] = useState(null); 
    const handleMouseEnter = () => setIsHovered(true);
    const handleMouseLeave = () => setIsHovered(false);
    const toggleMenu = (menuName) => {
        setOpenMenu(prevMenu => (prevMenu === menuName ? null : menuName));
    };

    function PopupMenu() {
        setVisible(!visible);
    }

      const handleNonAuthenticatedClick = (event) => {
        navigate('/Login');  
    };

    return(
        <header className='header'>
            <div className='header_cont'>
            <Link to='/'><h1><img src={h1Logo} alt='Gongcha logo'/></h1></Link>
                <div className='gnb'>
                    <nav>
                        <ul onMouseEnter={handleMouseEnter} onMouseLeave={handleMouseLeave}>
                            <li lass="menu">
                                <a href="#none">Brand</a>
                                <ul>
                                    <li><a href='#none'> </a></li>
                                    <li><a href='#none'> </a></li>
                                    <li><a href='#none'> </a></li>
                                </ul>
                            </li>

                            <li>
                                <a href='#none'>Menu</a>
                                <ul>
                                    <li><a href='#none'></a></li>
                                    <li><Link to='./MenuDrink'></Link></li>
                                    <li><a href='#none'> &#183; MD</a></li>
                                    <li><a href='#none'></a></li>
                                </ul>
                            </li>

                            <li>
                                <a href='#none'>Store</a>
                                <ul>
                                    <li><Link to='./Store'> </Link></li>
                                    <li><a href='#none'> </a></li>
                                </ul>
                            </li>

                            <li>
                                <a href='#none'>Membership</a>
                                <ul>
                                    <li><a href='#none'> </a></li>
                                    <li><a href='#none'>  &#38; e-Gift</a></li>
                                    <li><a href='#none'>FAQ</a></li>
                                </ul>
                            </li>

                            <li>
                                <a href='#none'>Benefit</a>
                                <ul>
                                    <li><a href='#none'> </a></li>
                                    <li><a href='#none'> </a></li>
                                </ul>
                            </li>

                            <li>
                                <a href='#none'>Event&#38;News</a>
                                <ul>
                                    <li><a href='#none'></a></li>
                                    <li><a href='#none'></a></li>
                                </ul>
                            </li>
                        </ul>
                    </nav>
                </div>

                <div className='util_menu01'>
                    {isAuthenticated ? (
                        <>
                         <Link to='/Mypage'>Mypage</Link>
                         <span>  </span>
                        <button onClick={handleLogout}>Log out</button>
                        </>
                    ) : (
                        <>
                            <Link to='/Login'>Log in</Link>
                            <span>  </span>
                            <Link to='/Join'>Join</Link>
                        </>
                    )}
                </div>
            </div>

            <div className='hamburger_menu'>
                <button onClick={PopupMenu}><img src={menuIcon} alt="menu" className='menu'/></button>
                
                <div className={`main_go_to ${visible ? 'show' : 'hide'}`}>
                    <h2><img src={h2Logo} alt="Gongcha logo" /></h2>
                    <ul>
                        <li>
                            <a href='#none' onClick={(e) => {e.preventDefault(); toggleMenu('brand');}}>Brand</a>
                            {openMenu === 'brand' && (
                            <ul>
                                <li><a href='#none'> </a></li>
                                <li><a href='#none'> </a></li>
                                <li><a href='#none'> </a></li>
                            </ul>
                            )}
                        </li>

                        <li>
                            <a href='#none' onClick={() => toggleMenu('menu')}>Menu</a>
                            {openMenu === 'menu' && (
                            <ul>
                                <li><a href='#none'></a></li>
                                <li><Link to='./MenuDrink'></Link></li>
                                <li><a href='#none'> &#183; MD</a></li>
                                <li><a href='#none'></a></li>
                            </ul>
                            )}
                        </li>

                        <li>
                            <a href='#none' onClick={() => toggleMenu('store')}>Store</a>
                            {openMenu === 'store' && (
                            <ul>
                                <li><Link to='./Store'> </Link></li>
                                <li><a href='#none'> </a></li>
                            </ul>
                            )}
                        </li>

                        <li>
                            <a href='#none' onClick={() => toggleMenu('membership')}>Membership</a>
                            {openMenu === 'membership' && (
                            <ul>
                                <li><a href='#none'> </a></li>
                                <li><a href='#none'>  &#38; e-Gift</a></li>
                                <li><a href='#none'>FAQ</a></li>
                            </ul>
                            )}
                        </li>

                        <li>
                            <a href='#none' onClick={() => toggleMenu('benefit')}>Benefit</a>
                            {openMenu === 'benefit' && (
                            <ul>
                                <li><a href='#none'> </a></li>
                                <li><a href='#none'> </a></li>
                            </ul>
                            )}
                        </li>

                        <li>
                            <a href='#none' onClick={() => toggleMenu('eventNews')}>Event&#38;News</a>
                            {openMenu === 'eventNews' && (
                            <ul>
                                <li><a href='#none'></a></li>
                                <li><a href='#none'></a></li>
                            </ul>
                            )}
                        </li>
                    </ul>

                        <div className='util_menu02'> 
                            <div>
                                <a href='#none'></a>
                                <a href='#none'></a>  
                            </div>
                           
                           <div>
                                <Link to='/Login'>Log in</Link>
                                <Link to='/Join'>Join</Link>
                           </div>
                           
                           <div>

                            {isAuthenticated ? (
                                <>
                                    <a href='#none' onClick={handleLogout}>Log out</a>
                                    <Link to='/Mypage'>My page</Link>
                                </>
                            ) : (
                                <>
                                    <a href='#none' onClick={handleNonAuthenticatedClick}>Log out</a>
                                    <a href='#none' onClick={handleNonAuthenticatedClick}>My page</a>
                                </>
                            )}
                        </div>
                        </div>
                        <button onClick={PopupMenu} className='close_btn'><img src={CloseIcon} alt="" /></button>
                </div> 
            </div>
            <div className={`gnb_bg ${isHovered ? 'slide-down' : ''}`} ></div>
        </header>
    )
}
export default Header;