import { useForm } from 'react-hook-form';
import '../Join.css'
import h1Login from '../images/h1_logo.png'

function Join() {
  const {
    register,
    handleSubmit,
    formState: { errors },
    watch,
  } = useForm();

const onSubmit = (data) => {
  alert(' .');
  console.log(data);
};
  const password = watch('password');

  return (
    <main className='join'>
    <div className='join_banner'>
          <h2></h2>
      </div>

      <div className='join_form'>
      <h1><img src={h1Login} alt="logo" /></h1>
    <form onSubmit={handleSubmit(onSubmit)}>
     {}
     <p>
          <input
            type="email"
            placeholder=""
            {...register('email', {
              required: ' .',
              pattern: {
                value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                message: '   .',
              },
            })}
          />
        </p>
        {errors.email && <p>{errors.email.message}</p>}

        {}
        <p>
          <input
            type="password"
            placeholder="8   ."
            {...register('password', {
              required: ' .',
              minLength: {
                value: 8,
                message: '  8  .',
              },
            })}
          />
        </p>
        {errors.password && <p>{errors.password.message}</p>}

        {}
        <p>
          <input
            type="password"
            placeholder=" "
            {...register('confirmPassword', {
              required: '  .',
              validate: (value) =>
                value === password || '  .',
            })}
          />
        </p>
        {errors.confirmPassword && <p>{errors.confirmPassword.message}</p>}
      <button type="submit" className='join_btn' ></button>
    </form>
    </div>
    </main>
  );
}

export default Join;